import { ref, onMounted } from "vue";
import { ChatSquare, Coffee } from "@element-plus/icons-vue";
export default {
  name: "HomePage",
  setup() {
    // 动态打字内容
    const fullText = "Developer | Deep Learning Enthusiast | Computational Designer";
    const typedText = ref("");

    // 技能列表
    const skills = ref([{
      name: "Deep Learning",
      color: "#1ABC9C"
    },
    // 清新的绿色
    {
      name: "PyTorch",
      color: "#E74C3C"
    },
    // 深红色
    {
      name: "TensorFlow",
      color: "#3498DB"
    },
    // 柔和的蓝色
    {
      name: "Python",
      color: "#F39C12"
    },
    // 温暖的橙黄色
    {
      name: "JavaScript",
      color: "#F1C40F"
    },
    // 明亮的金黄色
    {
      name: "C#",
      color: "#95A5A6"
    },
    // 柔和的灰色
    {
      name: "Rhino",
      color: "#9B59B6"
    },
    // 雅致的紫色
    {
      name: "Grasshopper",
      color: "#16A085"
    },
    // 深青色
    {
      name: "Revit",
      color: "#2980B9"
    } // 深蓝色
    ]);

    // 联系方式 Popover 的可见性
    const isContactVisible = ref(false);

    // 要饭方式 Dialog 的可见性
    const isDialogVisible = ref(false);

    // 访问 GitHub 方法
    const visitGitHub = () => {
      window.open("https://github.com/YuP2905", "_blank");
    };

    // 动态打字效果
    const typeEffect = () => {
      let index = 0;
      const interval = setInterval(() => {
        if (index < fullText.length) {
          typedText.value += fullText[index];
          index++;
        } else {
          clearInterval(interval);
        }
      }, 50);
    };

    // 组件挂载时开始打字效果
    onMounted(() => {
      typeEffect();
    });
    return {
      typedText,
      skills,
      isContactVisible,
      isDialogVisible,
      visitGitHub,
      ChatSquare,
      Coffee
    };
  }
};